:root {
  --point-color: #ff0000;
  --gray-color: #666;
  --background-color: #000;
}

.login_page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100vw;
  height: 100vh;
}

.login_page form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: auto;
  width: 40%;
  max-width: 600px;
  min-width: 400px;
  height: 40%;
  max-height: 450px;
  min-height: 350px;
  border: 1px solid #bbb;
  border-radius: 30px;
  overflow: hidden;
}

.login_page .login_title {
  font-size: 2em;
  padding: 10% 0 5%;
}

.login_page .id {
  border: 1px solid #ddd;
  border-radius: 5px 5px 0 0;
}

.login_page .pw {
  border: 1px solid #ddd;
  border-top: 0;
  margin-bottom: 30px;
  border-radius: 0 0 5px 5px;
}

.login_page .id,
.login_page .pw {
  position: relative;
  width: 70%;
  padding: 10px 20px;
}

.login_page .id label,
.login_page .pw label {
  display: inline-block;
  height: 20px;
  width: 20px;
}

.login_page .id label svg,
.login_page .pw label svg {
  height: 100%;
  width: auto;
}

.login_page .id label path,
.login_page .pw label path {
  fill: #ddd;
}

.login_page .id input,
.login_page .pw input {
  position: relative;
  top: -5px;
  outline: none;
  width: calc(100% - 60px);
  height: 20px;
  margin-left: 10px;
  border: 0;
}

.login_page .id .login_delete,
.login_page .pw .login_delete {
  position: relative;
  top: 3px;
  width: 30px;
  height: auto;
  cursor: pointer;
}

.login_page .id .login_delete path,
.login_page .pw .login_delete path {
  fill: #ddd;
}

.login_page .search_my {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 280px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 0.8em;
  color: var(--gray-color);
}

.login_page .search_my li {
  position: relative;
  cursor: pointer;
}

.login_page .search_my li::after {
  content: "|";
  position: absolute;
  right: -20px;
  color: #ddd;
  cursor: auto;
}

.login_page .search_my li:last-child::after {
  content: "";
}

.login_page .submit_btn {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  border: 0;
  border-top: 1px solid #bbb;
  cursor: pointer;
}

.login_page .submit_btn:hover {
  background-color: #ddd;
}
